// main page elements
body {
	font-size: 14px;
	color: white;
	font-family: 'Rubik', sans-serif;
	font-weight: 400;
	background: white;
	overflow-x: hidden;
	background: #000205;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

a {
	color: white; }

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	position: relative;
	max-width: 1440px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1440px) {
		padding: 0 30px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 15px; } }

.flex {
	display: flex; }

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	border-radius: 12px;
	color: white; }
